/* Track */
.navds-switch__track {
  width: 44px;
  height: 24px;
  background-color: var(--navds-switch-color-track-background);
  position: absolute;
  top: var(--navds-spacing-3);
  left: 0;
  border-radius: var(--navds-border-radius-medium);
  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
}
